@charset "UTF-8";
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 200;
}
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-DemiBold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
}
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 800;
}
:root {
  font-family: "TT Lakes";
  font-size: 16px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background: #1b1c21;
}

.page {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  margin-inline: auto;
  min-width: 320px;
  max-width: 1440px;
  min-height: 1024px;
  padding-bottom: 1rem;
  background: #FFFFFF;
}

.page__content {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin-inline: auto;
  min-width: 320px;
  max-width: 1440px;
  min-height: 1024px;
  padding-bottom: 1rem;
  background: #F8F8F8;
}

.button {
  min-width: fit-content;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  font-weight: 600;
  font-weight: 200;
  background-position: center;
  background-repeat: no-repeat;
  background: transparent;
  border: none;
  cursor: pointer;
}

.button_type--border {
  height: 40px;
  padding: 0.5rem 1rem;
  background: #F8F8F8;
  color: #7E7E82;
}

.button_type--border--active {
  background: #FFFFFF;
  color: #7E7E82;
  padding: 0.5rem 1rem;
  border: 2px solid #B8FFEC;
  border-radius: 6px;
}

.button_type--circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #FF3E79;
  transition: 0.25s;
}

.button_type--circle:hover {
  transform: scale(0.9) rotate(5deg);
  box-shadow: 0 0 10px 0 rgba(150, 150, 150, 0.5), 0 0 15px 0px rgba(20, 20, 20, 0.5);
}

.button-arrow {
  color: #1B1C21;
}

.button-arrow::before {
  content: "";
  border-left: 2px solid #41F6D7;
  border-top: 2px solid #41F6D7;
  width: 5px;
  height: 5px;
  transition: rotate 1s;
  transform: rotate(-135deg);
  float: left;
  margin-top: 3px;
  margin-right: -16px;
}

.button-arrow::after {
  content: "";
  border-left: 2px solid #41F6D7;
  border-top: 2px solid #41F6D7;
  width: 5px;
  height: 5px;
  transition: rotate 1s;
  transform: rotate(-135deg);
  float: left;
  margin-top: 10px;
  margin-right: 16px;
}

.button-arrow--open::before {
  transition: 1s;
  transform: rotate(45deg) translate(0px, 0px);
}

.button-arrow--open::after {
  transition: 1s;
  transform: rotate(45deg) translate(0px, 0px);
}

