.button_type_text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 24px;
  background: #FFF;
  font-weight: 500;
  z-index: 100;
}

.button_type_text:hover {
  color: #7e7e82;
  transition: 0.3s;
}

.button_type_text::after {
  position: absolute;
  content: attr(data-name);
  font-size: 24px;
  font-weight: bold;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #82827e;
  opacity: 0;
  transition: 0.2s;
  z-index: 99;
}

.button_type_text:hover::after {
  opacity: 0.4;
  transform-origin: bottom left;
  transform: translate(20px, -25px);
}

.button_type_text::before {
  position: absolute;
  opacity: 0;
  content: "";
  float: left;
  width: 4px;
  height: 32px;
  left: 0px;
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
  background: #41F6D7;
}

.button_type_text:hover::before {
  opacity: 1;
  transition: 1s;
}

.button__text {
  color: #1b1c21;
  font-weight: 500;
}

.up-menu {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  min-height: 88px;
  background: #fff;
  border-bottom: 2px solid #D9FFF5;
}

.logo {
  margin: 0;
  padding: 0;
  width: 96.55px;
  height: 50.81px;
}

.logo__image {
  object-fit: contain;
}

.up-menu__left {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  flex-basis: 60px;
  margin-left: 1rem;
}

.up-menu__left::after {
  position: relative;
  content: "";
  width: 0px;
  height: 2rem;
  background-color: #EAEAEA;
  border: 1px solid #EAEAEA;
}

.up-menu__center {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-inline: 1rem;
  flex-basis: 100%;
}

.up-menu__social-menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.up-menu__right {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: 1rem;
  flex-basis: 100px;
  margin-right: 1rem;
}

.up-menu__right::before {
  position: relative;
  content: "";
  width: 0px;
  height: 2rem;
  background-color: #EAEAEA;
  border: 1px solid #EAEAEA;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  gap: 1.5rem;
  background: #F8F8F8;
  padding-bottom: 2rem;
  border-bottom: 2px solid #D9FFF5;
}

.main__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin-top: 24px;
}

.main__header::before {
  position: absolute;
  content: "";
  width: 4px;
  height: 40px;
  border-radius: 0 0.5rem 0.5rem 0;
  background: #41F6D7;
}

.header__text {
  font-size: 28px;
  line-height: 40px;
  letter-spacing: -0.6px;
  margin-left: 1rem;
}

.header__menu-small {
  display: none;
}

.main__h-scroll-menu {
  display: flex;
  flex-direction: row;
  max-width: 1120px;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 0.5rem;
  overflow-y: auto;
  scrollbar-width: none; /*скрыть скролбар в Geko*/
  margin-left: 12px;
}

@supports selector(::-webkit-scrollbar) {
  .main__h-scroll-menu::-webkit-scrollbar {
    /*скрыть скролбар в WebKit*/
    overflow: hidden;
  }
}
.main__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding-inline: 1.5rem;
}

.content__article {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 1rem;
  max-height: 160px;
  overflow: hidden;
}

.content__article-paragraph {
  font-weight: 100;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21;
}

.content__text-block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex: 0 0 50%;
}

.main__banner {
  display: flex;
  flex: 0 0 50%;
  max-width: 40%;
  border-radius: 0.5rem;
  object-fit: contain;
}

.content__button-arrow {
  margin-top: 1rem;
  margin-left: 0rem;
}

@media screen and (max-width: 740px) {
  .main {
    gap: 2rem;
  }
  .main__content {
    padding-inline: 0;
    gap: 48px;
  }
  .content__text-block {
    flex: 0 0 100%;
    padding-inline: 1rem;
  }
  .main__banner {
    flex: 0 0 100%;
    max-width: 100%;
  }
  .content__article {
    max-height: 160px;
  }
}
.service-swiper, .technique-swiper, .price-swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

.swiper-wrapper {
  margin-left: var(--pad-left);
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #DDDDDD;
}

.swiper-pagination-bullet-active {
  width: 12px;
  height: 12px;
  background-color: #B5B6BC;
}

.elements__item {
  display: flex;
}
.elements__item .element {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-inline: 1rem;
  border: 1px solid #EAEAEA;
  background: #FFFFFF;
