}
.elements__item .element .element__button::after {
  position: relative;
  display: block;
  content: "";
  width: 0.4rem;
  height: 0.4rem;
  border-top: 2px solid #FF3E79;
  border-right: 2px solid #FF3E79;
  transform: translateX(0.9rem) rotate(45deg);
}

.elements__item--fake {
