.modal--open {
  transition: 1s;
  transform: translateX(0px);
}

.modal--close {
  transition: 1s;
  transform: translateX(110%);
}

.modal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  background-color: #FFFFFF;
  padding-inline: 1rem;
  gap: 1rem;
  max-width: 440px;
  height: 100%;
  box-shadow: 1rem 0 4rem 0 rgba(14, 24, 80, 0.2), -2px 0 4px 0 rgba(69, 79, 126, 0.02);
}
.modal .header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 40px;
  margin-top: 1.5rem;
}
.modal .header .header__text {
  font-size: 24px;
  line-height: 40px;
  letter-spacing: -0.6px;
  margin-left: 0;
}
.modal .header .header__text::before {
  position: absolute;
  display: block;
  content: "";
  width: 4px;
  height: 40px;
  background: #41F6D7;
  border-radius: 0px 4px 4px 0;
  transform: translateX(-1rem);
}
.modal .modal__form {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  justify-content: flex-start;
  align-items: stretch;
  gap: 1rem;
}
.modal .modal__form .form__input {
  height: 3rem;
  border: 1px solid #EAEAEA;
  border-radius: 0.5rem;
  padding: 1rem;
  cursor: pointer;
}
.modal .modal__form .form__input:hover {
  box-shadow: 0 0 2px 2px rgba(65, 246, 215, 0.2);
}
.modal .modal__form .form__input:focus {
  border-color: #7E7E82;
}
.modal .modal__form .form__input::placeholder {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  font-weight: 600;
  color: #B5B6BC;
}
.modal .modal__form .form__agreement {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  font-weight: 200;
  color: #7E7E82;
}
.modal .modal__form .form__link {
  text-decoration: none;
  color: #FF3E79;
}
.modal .modal__form .form__submit {
  align-self: flex-end;
  font-size: 12px;
  font-weight: 800;
  line-height: 1.5rem;
  letter-spacing: -0.15px;
  text-align: left;
  text-transform: uppercase;
  width: 136px;
  height: 32px;
  padding-left: 1.25rem;
  padding-block: 0.25rem;
  border-radius: 1rem;
  border: none;
  background-color: #FF3E79;
  color: #FFFFFF;
  cursor: pointer;
}
.modal .modal__form .form__submit::after {
  position: absolute;
  content: "";
  width: 6px;
  height: 6px;
  border-top: 3px solid #FFFFFF;
  border-left: 3px solid #FFFFFF;
  transform: translate(0.5rem, 0.5rem) rotate(135deg);
}

.feedback {
  display: none;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-width: 320px;
  height: 1024px;
  background-color: rgba(255, 255, 255, 0.6);
  z-index: 10;
}
.feedback .modal .modal__form .input-message {
  height: 118px;
}

.call {
  display: none;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-width: 320px;
  height: 1024px;
  background-color: rgba(255, 255, 255, 0.6);
  z-index: 10;
}

@media screen and (min-width: 520px) {
  .header .mobile-menu__button-close {
    position: absolute;
    transform: translateX(-6rem);
  }
}
.modal-host {
  position: absolute;
  width: 100%;
  top: 0;
  left: -100%;
  background-color: rgba(255, 255, 255, 0.6);
  transition: left 1s, opacity 1s, background-color 2s;
  z-index: 9;
}
.modal-host .mobile-menu {
  display: flex;
  flex-direction: column;
  position: sticky;
  justify-content: flex-start;
  gap: 1.5rem;
  padding-top: 1rem;
  min-width: 320px;
  max-width: 360px;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  box-shadow: 1rem 0 4rem 0 rgba(14, 24, 80, 0.2), -2px 0 4px 0 rgba(69, 79, 126, 0.02);
}
.modal-host .mobile-menu .mobile-menu__header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background: #FFFFFF;
}
.modal-host .mobile-menu .mobile-menu__header .mobile-menu__logo {
  position: relative;
  left: -35px;
}
.modal-host .mobile-menu .mobile-menu__navigation {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 32px;
}
.modal-host .mobile-menu .mobile-menu__footer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 16px;
  margin-top: 0.5rem;
  padding-left: 24px;
}
.modal-host .mobile-menu .mobile-menu__footer .mobile-menu__footer-menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}
.modal-host .mobile-menu .mobile-menu__footer .mobile-menu__tel {
  text-decoration: none;
  color: #1B1C21;
  font-size: 24px;
  font-weight: bold;
  line-height: 32px;
  letter-spacing: -0.6px;
  cursor: pointer;
}
.modal-host .mobile-menu .mobile-menu__footer .mobile-menu__mail {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  font-weight: 600;
  text-decoration: none;
  color: #1B1C21;
  cursor: pointer;
}
.modal-host .mobile-menu .mobile-menu__footer .mobile-menu__lang-switcher {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 7px;
}
.modal-host .mobile-menu .mobile-menu__footer .lang-switcher__radio {
  display: none;
}
.modal-host .mobile-menu .mobile-menu__footer .lang-switcher__label {
  content: attr(value);
  color: #1B1C21;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  font-weight: 600;
  cursor: pointer;
  user-select: none;
}
.modal-host .mobile-menu .mobile-menu__footer .lang-switcher__radio:checked + label {
  color: #7e7e82;
}

.modal-host_open {
  left: 0;
  background-color: rgba(255, 255, 255, 0.6);
}

@media screen and (min-width: 769px) {
  .modal-host .mobile-menu {
    box-shadow: none;
  }
  .modal-host .mobile-menu .mobile-menu__footer {
    margin-top: 241px;
  }
  .modal-host .mobile-menu .mobile-menu__footer .mobile-menu__lang-switcher {
    margin-top: 40px;
  }
}
@media screen and (max-width: 1119px) {
  .modal-host {
    position: absolute;
  }
}
@media screen and (min-width: 1120px) {
  .modal-host {
    position: static;
    float: left;
    opacity: 1;
    background-color: #FFFFFF;
    width: 320px;
  }
  .modal-host .mobile-menu__button-close {
    display: none;
  }
}
@media screen and (max-width: 350px) {
  .up-menu__left::after {
    display: none;
  }
}
@media screen and (max-width: 500px) {
  .up-menu__social-menu {
    display: none;
  }
}
@media screen and (min-width: 1120px) {
  .page__up-menu,
.fade-place__fade {
    display: none;
  }
  .main {
    gap: 2rem;
  }
  .header__menu-small {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;
    padding-right: 2rem;
  }
  .menu-small__button-define {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    gap: 0.5rem;
  }
  .main__h-scroll-menu {
    flex-wrap: wrap;
  }
}